#armatura-carousel {
  background: url(/storage/img/fondo-slider.jpg) center top no-repeat scroll;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
}

@media (min-height: 600px) and (max-height: 1080px) and (min-width: 1199px) {
  #armatura-carousel {
    min-height: 90vh;
  }
}

#armatura-carousel:after {
  z-index: -1;
  left: 50%;
  margin-left: -10px;
  content: '';
  width: 0;
  height: 0;
  position: relative;
  bottom: -8px;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #000000 transparent transparent transparent;
}

@media (min-height: 600px) and (max-height: 1080px) and (min-width: 1199px) {
  #armatura-carousel:after {
    position: absolute;
    top: 100%;
  }
}

#armatura-carousel h2 {
  color: #fff;
}

@media (max-width: 767px) {
  #armatura-carousel h2 {
    font-size: 16px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #armatura-carousel h2 {
    font-size: 22px;
  }
}

@media (min-width: 991px) {
  #armatura-carousel h2 {
    font-size: 28px;
  }
}

#armatura-carousel p {
  color: #bcbcbc;
}

@media (max-width: 767px) {
  #armatura-carousel p {
    font-size: 12px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #armatura-carousel p {
    font-size: 15px;
  }
}

@media (min-width: 991px) {
  #armatura-carousel p {
    font-size: 20px;
  }
}

#armatura-carousel-slide-3, #armatura-carousel-slide-2, #armatura-carousel-slide-1 {
  padding-top: 5vh;
}

#armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
  margin-left: auto;
  margin-right: auto;
}

@media (max-height: 1080px) and (min-width: 1199px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 60vh;
  }
}

@media (max-height: 1080px) and (min-width: 991px) and (max-width: 1199px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 300px;
  }
}

@media (max-height: 1080px) and (min-width: 767px) and (max-width: 991px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 240px;
  }
}

@media (max-height: 1080px) and (max-width: 767px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 120px;
  }
}

@media (min-height: 1080px) and (min-width: 1199px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 400px;
  }
}

@media (min-height: 1080px) and (min-width: 991px) and (max-width: 1199px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 300px;
  }
}

@media (min-height: 1080px) and (min-width: 767px) and (max-width: 991px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 250px;
  }
}

@media (min-height: 1080px) and (max-width: 767px) {
  #armatura-carousel-slide-3-img, #armatura-carousel-slide-2-img, #armatura-carousel-slide-1-img {
    height: 120px;
  }
