.hvr-icon-down {
  padding-right: 0px;
  padding-top: 25px;
  padding-bottom: 25px;
  color: #bcbcbc;
  cursor: pointer;
}

@media (max-width: 767px) {
  .hvr-icon-down {
    font-size: 12px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .hvr-icon-down {
    font-size: 16px;
  }
}

@media (min-width: 991px) {
  .hvr-icon-down {
    font-size: 22px;
  }
}

.hvr-icon-down:link, .hvr-icon-down:visited, .hvr-icon-down:hover, .hvr-icon-down:active {
  color: #bcbcbc;
  text-decoration: none;
}

.hvr-icon-down::before {
  content: "\f078";
  position: absolute;
  right: 0px;
  left: 0px;
  padding: 0px 1px;
  font-family: FontAwesome;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  color: #fff;
}

@media (max-width: 767px) {
  .hvr-icon-down::before {
    top: 50px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .hvr-icon-down::before {
    top: 50px;
  }
}

@media (min-width: 991px) {
  .hvr-icon-down::before {
    top: 60px;
  }
}

@media (min-width: 767px) {
  .display-table {
    display: table;
    table-layout: fixed;
  }
}

@media (min-width: 767px) {
  .display-cell {
    display: table-cell;
    vertical-align: middle;
    float: none;
  }
}
